.clock {
    width: 200px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: solid 1px #ccc;
    border-radius: 5px;
    margin: 5px;
    background: #ccc;
    float: left;
}
.digital-clock {
    padding: 10px;
}
.analog-clock {
    width: 100px;
    height: 100px;
    border: solid 2px #000;
    border-radius: 50%;
    position: relative;
    background: #fff;
}
.dial {
    position: absolute;
    left: 50%;
    width: 50px;
    height: 50px;
    transform-origin: bottom left;
}
.seconds {
     border-left:solid 1px green;
 }
.minutes {
     border-left:solid 1px #cc0000;
 }
.hours {
     width: 35px;
     height: 35px;
     top: 15px;
     border-left:solid 2px #000;
 }
